* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  padding: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.container {
  margin: 0 auto;
  width: 1200px;
}
@media screen and (max-width: 768px) {
  .container {
    margin: 0 auto;
    max-width: 720px;
    width: 100%;
  }
}
@media screen and (max-width: 567px) {
  .container {
    margin: 0 auto;
    max-width: 98%;
    width: 100%;
  }
}
.site_bar_wrapper {
  padding-top: 20px;
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translateX(-50%);
  background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
  min-height: 200px;
  z-index: 2;
}
.site_bar_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav_list {
  display: flex;
  gap: 40px;
}
.nav_list_item:first-child {
  position: relative;
  max-height: 50px;
  overflow: hidden;
}
.nav_list_item:first-child:after {
  content: url("../img/drop_arrow_icon.svg");
  position: absolute;
  top: 0;
  right: -20px;
  cursor: pointer;
}
.nav_list_item:last-child {
  position: relative;
  max-height: 50px;
  overflow: hidden;
}
.nav_list_item:last-child:after {
  content: url("../img/drop_arrow_icon.svg");
  position: absolute;
  top: 0;
  right: -20px;
  cursor: pointer;
}
.nav_list_item {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #232323;
}
.nav_list_item a {
