.nav_list_item:first-child:hover {
  max-height: unset;
  overflow: unset;
}
.nav_list_item:first-child:hover .drop_menu {
  display: flex;
  flex-direction: column;
  position: absolute;
  transition: 0.3s;
  opacity: 1;
  top: -10px;
  width: max-content;
  background: #FFFFFF;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  left: -20px;
}
.nav_list_item:last-child:hover {
  max-height: unset;
  overflow: unset;
}
.nav_list_item:last-child:hover .drop_menu_contact {
  display: flex;
  flex-direction: column;
  gap: 20px;
  position: absolute;
  transition: 0.3s;
  opacity: 1;
  top: -25px;
  width: max-content;
  background: #FFFFFF;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  padding: 20px;
  z-index: 2;
}
.drop_menu_item:hover {
  background: rgba(0, 44, 66, 0.1);
}
.our_advantage_group_img_item:hover,
.our_advantage_big_img_inner:hover {
  transform: scale(1.1);
}
.doc_img_inner img:hover {
  transform: scale(1.1);
}
.category_sub_inner:hover {
