.catalog_sliders_col_list_btn {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #232323;
  border: 1px solid #FFA800;
  width: 410px;
  margin-top: 50px;
  padding: 14px 63px;
  display: block;
}
.cat_sliders_popup_wrapper {
  display: none;
  min-width: 885px;
  border: 1px dashed black;
  margin: 0 auto;
}
.cat_sliders_popup_sub_wrapper {
  padding: 55px 69px;
  display: flex;
  flex-direction: column;
}
.cat_close_icon {
  width: 24px;
  height: 24px;
  cursor: pointer;
  margin-left: auto;
}
.cat_popup_top_title {
  font-family: Akzidenz-Grotesk Pro;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
  color: #424F60;
}
.cat_popup_top_text {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  /* identical to box height, or 20px */
  margin-top: 10px;
  color: #000000;
}
.cat_popup_bottom_block {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
.cat_popup_left_block {
  width: 49%;
}
.cat_popup_inner {
  width: 100%;
  display: flex;
  background: #F9F9F9;
}
.cat_popup_inner img {
  width: 180px;
  height: auto;
  margin: 0 auto;
  padding: 8px 0;
}
.cat_popup_descr_title {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #424F60;
}
.cat_form_right_block {
  width: 49%;
}
.catalog_sliders_col_item .swiper-pagination-catalog {
  position: absolute;
  left: 40%;
  bottom: 22px;
  z-index: 10;
}
.catalog_sliders_col_item .swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  background: #fff;
  cursor: pointer;
  opacity: 1;
  margin: 0 8px !important;
}
.catalog_sliders_col_item .swiper-pagination-bullet-active {
  width: 8px;
  height: 8px;
  background: #FFA800;
  opacity: 1;
}
