.site_bar_wrapper {
  padding-top: 20px;
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translateX(-50%);
  background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
  min-height: 200px;
  z-index: 2;
}

.site_bar_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav_list {
  display: flex;
  gap: 40px;
}

.nav_list_item:first-child {
  position: relative;
  max-height: 50px;
  overflow: hidden;
}

.nav_list_item:first-child:after {
  content: url("../img/drop_arrow_icon.svg");
  position: absolute;
  top: 0;
  right: -20px;
  cursor: pointer;
}

.nav_list_item:last-child {
  position: relative;
  max-height: 50px;
  overflow: hidden;
}

.nav_list_item:last-child:after {
  content: url("../img/drop_arrow_icon.svg");
  position: absolute;
  top: 0;
  right: -20px;
  cursor: pointer;
}

.nav_list_item {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #232323;
  //min-height: 50px;
}

.nav_list_item a {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #232323;
}

